import React from 'react';
import ReactDOM from 'react-dom';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import MySlider from './slider.js'
import Icon from '@material-ui/core/Icon';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import { CircleSlider } from 'react-circle-slider';

import './index.css';

class App extends React.Component {
	constructor(state) {
		super(state);
		this.state = {
			gradtype: false,
			bgcolor: [
				{ color: '#FF0BF2', stop: 0 },
				{ color: '#E7F62A', stop: 30 }
			],
			deg: '45'
		};
	}

	componentDidMount() {
		if(!localStorage.getItem('bgcolor1color1')) {
			localStorage.setItem('bgcolor1deg', this.state.deg);
		 	localStorage.setItem('bgcolor1color0', this.state.bgcolor[0].color);		 	
			localStorage.setItem('bgcolor1stop0', this.state.bgcolor[0].stop);
			localStorage.setItem('bgcolor1color1', this.state.bgcolor[1].color);
			localStorage.setItem('bgcolor1stop1', this.state.bgcolor[1].stop);
		} else {
			let bgcolor = [];
			for (let i = 0; i<(localStorage.length - 1) / 2; i++) {
				let bgcolor1color = localStorage.getItem('bgcolor1color' + [i]);
				let bgcolor1stop = localStorage.getItem('bgcolor1stop' + [i]);
				bgcolor.push({ color: bgcolor1color, stop: bgcolor1stop });				
			}
			this.setState({ bgcolor: bgcolor });
			this.setState({ deg: localStorage.getItem('bgcolor1deg') })
		}

		let bgcolorstr = '';
		if (this.state.gradtype) {
			bgcolorstr = `radial-gradient(circle`;			
		} else {
			bgcolorstr = `linear-gradient(` + this.state.deg + `deg`;		
		}		
		for (let i=0; i<this.state.bgcolor.length; i++) {
			bgcolorstr += ', ' + this.state.bgcolor[i].color + ' ' + this.state.bgcolor[i].stop + '%' 
		}
		bgcolorstr += ')';
		document.getElementById('colorstring').innerText = bgcolorstr + ';';
		document.body.style.background = bgcolorstr;			
		document.body.style.backgroundRepeat = 'no-repeat';
		document.body.style.backgroundAttachment = 'fixed';
		document.getElementById('color1').style.background = bgcolorstr;
	}

	/*function populateStorage() {
		localStorage.setItem('bgcolor1color1', this.state.bgcolor[1].color);
		localStorage.setItem('bgcolor1stop1', this.state.bgcolor[1].stop);
		localStorage.setItem('bgcolor1color2', this.state.bgcolor[2].color);
		localStorage.setItem('bgcolor1stop2', this.state.bgcolor[2].stop);		
	}*/

	/*function setBackground() {
		let bgcolor1color1 = localStorage.getItem('bgcolor1color1');
		let bgcolor1stop1 = localStorage.getItem('bgcolor1stop1');
		let bgcolor1color2 = localStorage.getItem('bgcolor1color2');
		let bgcolor1stop2 = localStorage.getItem('bgcolor1stop2');
		
		let bgcolor = this.state.bgcolor.slice();
		bgcolor[1].color = bgcolor1color1;
		bgcolor[1].stop = bgcolor1stop1;
		bgcolor[2].color = bgcolor1color2
		bgcolor[2].stop = bgcolor1stop2
		this.setState({ bgcolor: bgcolor })
	}*/

	componentDidUpdate() {
		localStorage.clear();
		localStorage.setItem('bgcolor1deg', this.state.deg);
		for (let i = 0; i<this.state.bgcolor.length; i++) {
			localStorage.setItem('bgcolor1color' + [i], this.state.bgcolor[i].color);		 	
			localStorage.setItem('bgcolor1stop' + [i], this.state.bgcolor[i].stop);
		}

		let bgcolorstr = '';
		if (this.state.gradtype) {
			bgcolorstr = `radial-gradient(circle`;			
		} else {
			bgcolorstr = `linear-gradient(` + this.state.deg + `deg`;		
		}		
		for (let i=0; i<this.state.bgcolor.length; i++) {
			bgcolorstr += ', ' + this.state.bgcolor[i].color + ' ' + this.state.bgcolor[i].stop + '%' 
		}
		bgcolorstr += ')';
		document.getElementById('colorstring').innerText = bgcolorstr + ';';
		document.body.style.background = bgcolorstr;			
		document.body.style.backgroundRepeat = 'no-repeat';
		document.body.style.backgroundAttachment = 'fixed';
		document.getElementById('color1').style.background = bgcolorstr;
	}

	setToRadial = () => {
		this.setState({ gradtype: true });
		document.getElementById('degSetter').style.display = 'none';
	}

	setToLinear = () => {
		this.setState({ gradtype: false });
		document.getElementById('degSetter').style.display = 'initial';
	}

	addNewColorCard = () => {
		let bgcolor = this.state.bgcolor.slice();
		bgcolor.push({ color: '#F8F7F7', stop: '80' });
		this.setState({ bgcolor: bgcolor })
	}

	onColorChange = (event) => {
		const bgcolor = this.state.bgcolor.slice();
		bgcolor[event.target.name].color = event.target.value.toUpperCase();
		this.setState({ bgcolor: bgcolor })	
	}

	onRemoveCard = (event) => {		
		const bgcolor = this.state.bgcolor.slice();
		bgcolor.splice(event.target.parentElement.parentElement.firstChild.firstChild.name, 1);
		this.setState({ bgcolor: bgcolor })
	}

	onDegChange = (event) => {
		this.setState({ deg: event })
	}

	onHover = () => {
		document.getElementById('controlbar').style.opacity = '1.0'
	}

	onHoverOut = () => {
		document.getElementById('controlbar').style.opacity = '0.3'
	}

	onStopPointChange = name => (e, value) => {
		const bgcolor = this.state.bgcolor.slice();
		bgcolor[name].stop = value;
		this.setState({ bgcolor: bgcolor })
	};

	render() {	
		return(
			<Box style={{ height: '100vh' }} display="flex" flexDirection="column" alignItems='center' justifyContent="space-evenly">
				<h1>Background Generator</h1>
				<div id='colorstring' style={{maxWidth: '70vw'}}></div>
				<Box display='flex' flexDirection='column' className='background' id='controlbar' onMouseOver={this.onHover} onMouseLeave={this.onHoverOut} >
					<Box display="flex" justifyContent="center" >
						<Controll gradTypeR={this.setToRadial} gradTypeL={this.setToLinear} />
						<Box display='flex' flexDirection='column' justifyContent="space-evenly" className='degSetter background' id='degSetter'>
							<CircleSlider 
								onChange={this.onDegChange} 
								value={this.state.deg} 
								size={80}
								max='361' 
								shadow={false}
								knobRadius={6}
								knobColor="#ff5722" 
								showTooltip={true}
								tooltipSize={25} 
								progressColor="#FDB11B"
								progressWidth={10}
								circleWidth={10}
							 />
						</Box>
						<ColorCardList 
							bgcolor={this.state.bgcolor} 
							colorChange={this.onColorChange} 
							stopPointChange={this.onStopPointChange} 
							removeCard={this.onRemoveCard}
						 />
						<Box display='flex' className='background'>
							<button type='button' onClick={this.addNewColorCard} className='addColor'>+</button>					
						</Box>					
					</Box>
					<Box>
						<Divider variant="middle" />
					</Box>
					<Box display='flex' justifyContent="space-evenly" >
						<div className='colorBox' id='color1' />
						<div className='colorBox' id='color2' />
						<div className='colorBox' id='color3' />
					</Box>
				</Box>
			</Box>
		);
	}
}

class ColorCardList extends React.Component {
	render() {
		return (
			<Box display="flex" flexDirection="row" flexWrap='wrap' style={{ maxWidth: '68vw'}}>
				{
					this.props.bgcolor.map((color, i) => {
						return (
							<ColorCard 
								key={i}
								num={i}
								color={this.props.bgcolor[i].color} 
								stop={this.props.bgcolor[i].stop}
								colorChange={this.props.colorChange} 
								stopPointChange={this.props.stopPointChange}
								removeCard={this.props.removeCard}
							/>
						);
					})
				}
			</Box>
		);
	}
}

class ColorCard extends React.Component {
	render() {
		return(
			<Box className='background' display="flex" flexDirection="column" alignItems="center" justifyContent="space-evenly">
				<Box display="flex" flexDirection="row" justifyContent='space-between' style={{width: '100%'}}>
					<input 
						className="color" 					
						type="color"
						name={this.props.num}
						value={this.props.color} 
						onChange={this.props.colorChange} 
					/>					
					<button type='button' onClick={this.props.removeCard} className='closeColor'>X</button>				
				</Box>
				 <MySlider
				 	stop={this.props.stop}
				 	stopPointChange={this.props.stopPointChange(this.props.num)}
				 />
			</Box>
		);
	}
}

class Controll extends React.Component {
	render() {
		return(
			<Box className='background' display="flex" flexDirection="column" justifyContent="space-evenly">
				<button type='button' onClick={this.props.gradTypeL}><Icon fontSize="small" >texture</Icon><h4>Linear</h4></button>
				<button type='button' onClick={this.props.gradTypeR}><RadioButtonCheckedIcon fontSize="small"/><h4>Radial</h4></button>
			</Box>
		);
	}
}


ReactDOM.render(<App />, document.getElementById('root'));